import React from 'react'
import './portofolio.css'
import Web from './portofolioweb.png'
import Konten from './portofoliokonten.png'

const portofolio = () => {
  return (
    <div className='portofolio'>
      <h2>Portofolio</h2>
      <img className="web" src={Web} alt="Profil"></img>
      <img className="content" src={Konten} alt="Profil"></img>
      <br/>
      <img className="web" src={Web} alt="Profil"></img>
      <img className="content" src={Konten} alt="Profil"></img>
    </div>
  )
}

export default portofolio
