import React from 'react'
import { Component } from 'react'
// import { data } from './data'
import './navbar.css'
import Scrollspy from 'react-scrollspy'
// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

class navbar extends Component {
  state= {click: false};
  handleclick =()=>{
    this.setState({click : !this.state.click})
  }
  render() {
    return(
      <nav className='NavItem'>
        <h1 className='brand'>N A U F A L - R</h1>
        <div className='icon-menu' onClick={this.handleclick}>
          <i className={this.state.click ? 'fa fa-times' : 'fa fa-bars'}></i>
        </div>
        <ul className={this.state.click ? "menu active" : "menu"}>
          <div className='isi'>
            <Scrollspy className="scrollspy" items={ ['section-1', 'section-2', 'section-3', 'section-4'] } currentClassName="isCurrent">
              <a href='#section-1' className='nav-links'> Beranda </a>
              <a href='#section-2' className='nav-links'> Keahlian </a>
              <a href='#section-3' className='nav-links'> Portofolio </a>
              <a href='#section-4' className='nav-links'> Kontak </a>
            </Scrollspy>
          </div>
          {/* {data.map((item, index) => {
            return (
              <div className='isi' key={index}>
                <a  href={item.url} className={item.class}>
                    {item.title}
                </a>
              </div>
            )
          })} */}
        </ul>
      </nav>
    )
  }

}

export default navbar
